@mixin font-face($family, $path, $name, $svg, $weight: normal, $style: normal) {
  @font-face {
    font-family: $family;
    src: url('#{$path}/#{$name}.eot');
    src: url('#{$path}/#{$name}.eot?#iefix') format('embedded-opentype'), url('#{$path}/#{$name}.woff') format('woff'), url('#{$path}/#{$name}.ttf') format('truetype'), url('#{$path}/#{$name}.svg##{$svg}') format('svg');
    font-weight: $weight;
    font-style: $style; } }


/* font-family: "MyriadArabic - Regular"; */
@include font-face('MyriadArabic', '../fonts/MyriadArabic', 'MyriadArabic-Regular', 'MyriadArabic-Regular');

/* font-family: "MontserratAlternates Regular" */
@include font-face('MontserratAlternates', '../fonts/MontserratAlternatesRegular', 'MontserratAlternates-Regular', 'MontserratAlternates-Regular');

/* font-family: "MontserratAlternates Black" */
@include font-face('MontserratAlternates', '../fonts/MontserratAlternatesBlack', 'MontserratAlternates-Black', 'MontserratAlternates-Black', $weight: 900);

